import { VFC } from 'react';
import Icon from './cvg/icon';

type Color =
  | 'primary'
  | 'secondary'
  | 'success'
  | 'info'
  | 'danger'
  | 'warning'
  | 'light'
  | 'dark'
  | 'white';

type Size =
  | 'sm'
  | 'md'
  | 'lg'

interface ButtonProps {
  color?: Color;
  isGradient?: boolean;
  isOutline?: boolean;
  size?: Size;
  label: string;
  icon?: string;
}

const getBackgroundColor = (props: ButtonProps): string => {
  const { color, isGradient, isOutline } = props;
  const buttonColor = color || 'primary';

  if (isOutline) {
    return `btn-outline-${buttonColor}`;
  }

  return `btn${isGradient ? '-gradient' : ''}-${buttonColor}`;
};

const getSize = (props: ButtonProps): string => {
  const { size } = props;
  const buttonSize = size || 'md';
  switch (buttonSize) {
    case 'sm': return 'btn-sm';
    case 'lg': return 'btn-lg';
    default: return '';
  }
}

const Button: VFC<ButtonProps> = (props) => {
  const { label } = props;
  const backgroundColor = getBackgroundColor(props);
  const size = getSize(props);
  const className = `btn ${backgroundColor} ${size}`;

  return (
    <button
      type="button"
      className={`${className} w-auto me-1 mb-0`}
    >
      <div className="d-flex align-items-center">
        <Icon />
        {label}
      </div>
    </button>
  );
};

export default Button;
